@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700&display=swap);
.blog__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 85%;
  margin: 0 auto;
  padding: 10rem 17rem;
}

.blog__header {
  letter-spacing: -1px;
  font-size: 35px;
  padding-top: 3rem;
  text-transform: capitalize;
}

.blogs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 4rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 4rem 0;
}

.blog {
  max-width: 45rem;
  min-width: 40rem;
  height: 100%;
  margin: 0 auto;
  background: #fff;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  color: black;
  transition: box-shadow 0.3s ease-in;
}

.blog:hover {
  box-shadow: 1px 1px 50px rgba(0, 0, 0, 0.5);
}

.blog__content {
  margin-top: 1.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0 3rem;
}

.blog > img {
  max-width: 100%;
  width: 100%;
  height: 25rem;
  object-fit: cover;
}

.source-name {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.source-name > span {
  display: inline;
  background: rgba(38, 0, 128, 0.226);
  font-size: 1.1rem;
  padding: 0.5rem 0.6rem;
  color: rgba(0, 11, 43, 0.534);
  border-radius: 3px;
}

.source-name > p {
  font-size: 10px;
  font-weight: 500;
  padding-left: 10px;
}

.blog__title {
  font-size: 25px;
  letter-spacing: -0.6px;
  line-height: 1.3em;
  padding: 10px 0;
  font-weight: 700;
  text-decoration: underline;
}

.blog__description {
  font-size: 13px;
  line-height: 1.6em;
  color: rgb(90, 90, 90);
  padding-bottom: 30px;
}

.no__blogs {
  font-size: 25px;
  font-weight: 500;
  line-height: 1.5em;
}

.home {
  height: 87vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.login__message {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.login__message > h2 {
  font-size: 4rem;
  margin-bottom: 0;
}

.login__message > h1 {
  font-size: 12rem;
  width: 100%;
  line-height: 1em;
  margin: 0 auto 1.5rem;
  font-weight: 900;
  letter-spacing: 3px;
  color: rgb(17, 17, 17);
}

.login__message > h1 > span {
  color: #005b96;
  margin: -5px;
}

.login__message > p {
  padding: 1.5rem 0;
  line-height: 1.5em;
  width: 100%;
  margin: 0 auto;
  font-size: 1.8rem;
  font-family: "Roboto Mono", monospace;
}

.login__button {
  width: 30rem;
  margin: 1.5rem auto 0 auto;
  padding: 2rem 0;
  border: none;
  background: rgb(17, 17, 17);
  font-family: "Inter";
  color: white;
  font-size: 1.5rem;
  font-weight: 500;
  cursor: pointer;
}

.navbar {
  display: -webkit-flex;
  display: flex;
  position: fixed;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 10vh;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 5rem;
  background-color: #1b1b1d;
}

.navbar__header {
  color: #fff;
  font-size: 3.5rem;
  letter-spacing: 3px;
}

.navbar__header > span {
  margin: -5px;
}

.blog__search {
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 100%;
  max-width: 60rem;
  display: -webkit-flex;
  display: flex;
  padding: 0.5rem 0;
  margin: 0px 70px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: rgb(39, 39, 39);
  border-radius: 5px;
}

.search__input {
  width: 100%;
  padding: 0.5rem 3.5rem;
  padding-left: 1.5rem;
  background: none;
  font-family: "Roboto Mono", monospace;
  border: none;
  outline: none;
  font-size: 1.5rem;
  color: #fff;
}

.search-button {
  width: 18%;
  font-family: "Roboto Mono", monospace;
  background: none;
  color: white;
  font-size: 1.3rem;
  border: 1px solid #525252;
  cursor: pointer;
  margin-right: 0.5rem;
  padding: 1rem 0;
  border-radius: 4px;
  transition: 0.1s ease-in-out;
  background-color: rgb(31, 31, 31);
  outline: none;
}

.search-button:hover {
  background-color: rgb(31, 31, 31, 0.4);
}

.navbar__user-data {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.user {
  cursor: pointer;
  border: 2px solid #6497b1;
}

.signedIn {
  color: #fff;
  font-size: 1.5rem;
  padding-left: 1rem;
  cursor: pointer;
}

.logout__button {
  margin-left: 2.5rem;
  padding: 1.2rem 2.5rem;
  border-radius: 2rem;
  border: none;
  background: rgb(39, 39, 39);
  font-family: "Roboto Mono", monospace;
  color: white;
  font-size: 1.4rem;
  font-weight: 500;
  cursor: pointer;
}

.notSignedIn {
  font-size: 1.5rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

body {
  /* font-family: "Inter", sans-serif; */
  /* font-family: "Roboto Mono", monospace; */
}

@media screen and (max-width: 1520px) {
  .blog__container {
    padding: 10rem 15rem;
  }

  .blogs {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 1020px) {
  .blog__container {
    padding: 10rem 5rem;
  }

  .blogs {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media only screen and (max-width: 640px) {
  .blog__container {
    padding: 10rem 1rem;
  }
}

